.border-radius-24 {
  border-radius: 24px;
}

.gradient-background-v2 {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  // overflow: hidden;
  z-index: -1;
  opacity: 0.5;

  div:nth-child(2) {
    position: absolute;
    border-radius: 56%;
    height: 60%;
    width: 40%;
    top: 25%;
    left: 30%;
    background-color: map.get($colors, "primary-100");
    animation: move-left-to-right 10s infinite;
    animation-delay: 0.5s;
    filter: blur(150px);
  }

  div:nth-child(1) {
    position: absolute;
    border-radius: 56%;
    height: 60%;
    width: 40%;
    top: 25%;
    left: 15%;
    background-color: map.get($colors, "secondary-100");
    animation: move-left-to-right 10s infinite;
    animation-delay: 0.5s;
    filter: blur(150px);
  }

  &::after {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    z-index: -1;
  }
}

@keyframes move-left-to-right {
  0% {
    transform: translateX(-5%);
  }
  50% {
    transform: translateX(105%);
  }
  100% {
    transform: translateX(-5%);
  }
}

